import React, { useState, useEffect, useRef, useCallback } from 'react'

import config from '../../../config/config'
import LayoutsConfig from './Layouts.config'
import { moveKeyInArray, sortArray } from '../../../helpers/utils'

import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
//import Devices from '../../../controller/Devices'
import { ModuleConfigurations, ModuleSettings } from '../../../modules/'

import Badge from '@material-ui/core/Badge'
import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Typography from '@material-ui/core/Typography'

import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import DeleteIcon from '@material-ui/icons/Delete'
import DevicesIcon from '@material-ui/icons/Devices'
//import DragIcon from '@material-ui/icons/DragIndicator'
import DividerIcon from '@material-ui/icons/Remove'
import GroupIcon from '@material-ui/icons/FormatListBulleted'

//import DragDrop from '../../../components/DragDrop'
import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'
import Placeholder from '../../../components/Placeholder'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	iconContainer: {
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center'
	},
	iconCentered: {
		verticalAlign: 'middle',
		marginRight: theme.spacing(2)
	},
	noPadding: {
		padding: 0,
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	},
	height: {
		lineHeight: '45px'
	},
	title: {
		lineHeight: '25px'
	},
	Badge: {
		right: 'unset',
		transform: 'none',
	}
}));


const _copy = require('rfdc')();
const WidgetPage = React.memo(props => {
	const classes = useStyles();
	
	const [errors,setErrors] = useState({});
	const EDITOR = useRef({ moduleConfig: {}, ..._copy(props.widget) });
	console.log({ ...EDITOR.current }, { ..._copy(props.widget) });
	
	/*
	 *
	 */
	if (props.widget === null) {
		return null;
	}
	
	/**
	 *
	 */
	const _onClose = () => {
		EDITOR.current = null;
		props.onClose();
	}
	
	/**
	 *
	 */
	const _onSave = action => {
		
		// errors
		const requiredFields = [{ parameter: 'title', required: true }, { parameter: 'module', required: true }];
		let err = {};
		
		[ ...requiredFields, ...(ModuleConfigurations[props.widget.module] || []) ].forEach(field => {
			if (field.required === true && !EDITOR.current[field.parameter] && !EDITOR.current.moduleConfig[field.parameter]) {
				err[field.parameter] = true;
			}
		});
		
		if (Object.values(err).indexOf(true) > -1) {
			setErrors(prevErrors => ({ ...prevErrors, ...err }));
			return false;
		}
		
		// attach devices
		props.onSave(EDITOR.current);
	}
	
	/**
	 *
	 */
	const _onChange = useCallback((props, val, device) => {
		
		// error occured
		if (props.error && props.error !== errors[props.id]) {
			setErrors(prevErrors => ({
				...prevErrors,
				[props.id]: props.error
			}));
		}
		
		// no error -> remove old error
		else if (!props.error && errors[props.id]) {
			setErrors(prevErrors => {
				let nextErrors = { ...prevErrors }
				delete nextErrors[props.id];
				return nextErrors;
			});
		}
		
		// device
		if (device !== undefined) {
			if (props.id.indexOf('componentConfig.') > -1) {
				EDITOR.current.devices[device].componentConfig = EDITOR.current.devices[device].componentConfig || {}
				EDITOR.current.devices[device].componentConfig[props.id.replace('componentConfig.', '')] = val;
			}
			else {
				EDITOR.current.devices[device][props.id] = val;
			}
		}
		
		// converted nested IDs to object
		else if (props.id.indexOf('moduleConfig.') > -1) {
			EDITOR.current.moduleConfig[props.id.replace('moduleConfig.', '')] = val;
		}
		
		// set value
		else {
			EDITOR.current[props.id] = val;
		}
		
	}, [errors]);
	
	/**
	 *
	 */
	const _validate = useCallback((key, val) => {
		_onChange({ error: !val, id: key }, val || '');
	}, [_onChange]);
	
	/*
	 *
	 */
	const mount = () => _validate('title', props.widget.title); // trigger initial validation
	useEffect(mount, []);
	
	return (
		<>

			<Dialog
				header={i18n.t('Widget configuration')}
				actions={
					<Grid container>
						<Grid item xs={6}>
							{props.widget.index !== undefined && <Inputs.Button key='action-delete' variant="text" onClick={() => props.onDelete(props.widget.index)} color="secondary" startIcon={<DeleteIcon />} label="Delete Widget" />}
							&nbsp;
						</Grid>
						<Grid item xs={6} className={classes.alignRight}>
							<Inputs.Button key="action-cancel" variant="text" onClick={_onClose} color="secondary" label="Cancel" />
							<Inputs.Button key="action-save" variant="text" onClick={_onSave} color="primary" disabled={Object.keys(errors).length > 0} label="Apply" />
						</Grid>
					</Grid>
				}
				
				dense
				maxWidth='lg'
				fullWidth={true}
				open={true}
				onClose={_onClose}
				>
				
				<SectionWidget
					{...props}
					_onChange={_onChange}
					_validate={_validate}
					editor={EDITOR.current}
					errors={errors}
					/>
				
				<SectionModule
					{...props}
					_onChange={_onChange}
					editor={EDITOR.current}
					errors={errors}
					/>
				
			</Dialog>
		</>
	)
});

export default WidgetPage;


/*
 *
 */
const SectionWidget = React.memo(props => {
	Log._('WidgetPage', 'RENDER SectionWidget', 'development');
	//const classes = useStyles();
	const { _onChange, _validate, editor, widget } = props;
	
	const [fullscreen,setFullscreen] = useState(props.widget.fullscreen !== undefined ? widget.fullscreen : false);
	
	return (
		<Inputs.Fieldset label='Widget-Box'>
			<Inputs.FieldsetItem columns={12}>
				<Inputs.Switch
					key='fullscreen'
					id='fullscreen'
					{...LayoutsConfig.widgetFullscreen}
					
					onChange={(props, val) => {
						_onChange(props, val);
						
						const title = '(' + i18n.t('fullscreen') + ')';
						if (!editor.title && val === true) {
							_onChange({ 'id': 'title'}, title);
						}
						else if (editor.title === title && val === false) {
							_validate('title', '');
						}
						
						setFullscreen(val);
					}}
					val={fullscreen}
					/>
					
			</Inputs.FieldsetItem>
			
			{fullscreen !== true && <>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='title'
					id='title'
					{...LayoutsConfig.widgetTitle}
					
					autoFocus
					onChange={_onChange}
					val={editor.title || ''}
					/>
					
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='icon'
					id='icon'
					{...LayoutsConfig.widgetIcon}
					
					onChange={_onChange}
					val={editor.icon || ''}
					/>
					
			</Inputs.FieldsetItem>
			<Inputs.FieldsetItem columns={4}>
				<Inputs.Text
					key='iconStyle'
					id='iconStyle'
					{...LayoutsConfig.widgetIconStyle}
					
					onChange={_onChange}
					val={editor.iconStyle ? JSON.stringify(editor.iconStyle) : ''}
					/>
					
			</Inputs.FieldsetItem>
			</>}
		</Inputs.Fieldset>
	);
});

/*
 *
 */
const SectionModule = React.memo(props => {
	Log._('WidgetPage', 'RENDER SectionModule', 'development');
	//const classes = useStyles();
	const { _onChange } = props;
	
	const [module,setModule] = useState(props.widget.module || config.get('DEFAULT_MODULE'));
	
	return (
		<Inputs.Fieldset label='Module'>
			<Inputs.FieldsetItem>
				<Inputs.Text
					key='module'
					id='module'
					{...LayoutsConfig.widgetModule}
					
					autoComplete
					autoHighlight
					
					val={module}
					onChange={(props, val) => {
						_onChange(props, val);
						setModule(val);
					}}
					/>
				
			</Inputs.FieldsetItem>
			
			<ModuleConfig
				{...props}
				moduleConfigurations={(module && ModuleConfigurations[module]) || []}
				/>
			
			<ModuleDevices
				{...props}
				moduleSettings={(module && ModuleSettings[module]) || {}}
				/>
			
		</Inputs.Fieldset>
	);
});

/*
 *
 */
const ModuleConfig = React.memo(props => {
	Log._('WidgetPage', 'RENDER ModuleConfig', 'development');
	//const classes = useStyles();
	const { _onChange, moduleConfigurations, errors, editor } = props;
	
	// get module configuration options
	const moduleConfig = moduleConfigurations.map(config => {
	
		// consider required fields
		if (!errors['moduleConfig.' + config.parameter] && config.required === true && !(editor.moduleConfig[config.parameter] || config.val)) {
			_onChange({ error: true, id: 'moduleConfig.' + config.parameter }, (editor.moduleConfig[config.parameter] || config.val) || ''); // trigger initial validation
		}
		
		// render config option
		const Input = Inputs[config.type || 'Text'];
		return (
			<Inputs.FieldsetItem columns={4} key={'FieldsetItem-' + config.parameter}>
				<Input
					{...config}
					key={'moduleConfig-' + config.parameter}
					id={'moduleConfig.' + config.parameter}
					label={config.label || config.parameter}
					info={config.description}
					
					onChange={_onChange}
					val={editor.moduleConfig[config.parameter] !== undefined ? editor.moduleConfig[config.parameter] : config.val}
					/>
				
			</Inputs.FieldsetItem>
		);
	});
	
	return (
		<>
			<Inputs.FieldsetItem noPadding>
				<Inputs.FieldsetLegend label='Module Configuration' variant="h4" divider={false} />
			</Inputs.FieldsetItem>
			
			{moduleConfigurations.length > 0 ? moduleConfig : <>
				<Inputs.FieldsetItem noPadding>
					<Typography>{i18n.t('No configuration available for this module')}.</Typography>
				</Inputs.FieldsetItem>
			</>}
			
		</>
	);
});

/*
 *
 */
const ModuleDevices = React.memo(props => {
	Log._('WidgetPage', 'RENDER SectionDevices', 'development');
	const classes = useStyles();
	const { _onChange, moduleSettings } = props;
	
	const [devices,setDevices] = useState(props.widget.devices || []);
	const [error,setError] = useState('');
	useEffect(() => {
		_onChange({ id: 'devices' }, devices);
		
	}, [_onChange, devices]);
	
	if (!moduleSettings.devices) {
		return null;
	}
	
	/**
	 *
	 */
	const _addItem = (type, val) => {
		let device = { type: type }
		
		// add device
		if (type === 'device') {
			device.deviceId = val.id;
		}
		
		// add group
		else if (type === 'group') {
			device.name = val;
		}
		
		setError('');
		//editor.devices.push(device);
		setDevices(prevDevices => ([
			...prevDevices,
			device
		]));
	}
	
	/**
	 *
	 */
	const _editItem = (index, props, val) => _onChange(props, val, index);
	
	/**
	 *
	 */
	const _moveItem = (action, index) => {
		
		//editor.devices = moveKeyInArray([ ...editor.devices || [] ], index, action);
		setDevices(prevDevices => moveKeyInArray([ ...prevDevices ], index, action));
	}
	
	/**
	 *
	 */
	const _deleteItem = index => {
		
		//editor.devices.splice(index, 1);
		setDevices(prevDevices => {
			let nextDevices = [ ...prevDevices ];
			nextDevices.splice(index, 1);
			return nextDevices;
		});
	}
	
	const deviceOptions = sortArray(Object.values(props.devices), 'function');
	const deviceIdList = devices.map(device => device.deviceId);
	
	const renderedDevices = devices.map((device, i) => {
		return <Device
				{...props}
				key={device.type + '- ' + (device.deviceId || device.name) + '-' + i}
				_addItem={_addItem}
				_editItem={_editItem}
				_moveItem={_moveItem}
				_deleteItem={_deleteItem}
				device={props.devices[device.deviceId] || null}
				options={{ ...device, index: i }}
				devices={devices}
				/>
	});
	
	return (
		<>
			<Inputs.FieldsetItem noPadding>
				<Inputs.FieldsetLegend label='Assigned Devices' variant="h4" divider={false} />
			</Inputs.FieldsetItem>
			
			<Inputs.Fieldset noPadding>	
				<Inputs.Fieldset noPadding style={{ position: 'sticky', backgroundColor: '#fafafa', top: -16, zIndex: 1 }}>	
					<Inputs.FieldsetItem columns={3}>
						<Inputs.Text
							width="90%"
							key='addDevice'
							id='addDevice'
							label='Add device'
							
							error={error !== ''}
							errorMessage={error}
							
							autoComplete
							autoHighlight
							disableCloseOnSelect
							clearOnSelect
							clearOnBlur
							
							options={deviceOptions}
							getOptionLabel={option => i18n.t(option.function) + ' ' + option.name + (!option.attributes || !option.attributes.manufacturer ? '' : ' (' + option.attributes.manufacturer.name + ')')}
							renderOption={(option, state) => {
								const added = deviceIdList.indexOf(option.id) > -1 ? deviceIdList.filter(device => device === option.id).length : 0;
								return <>
									<Badge badgeContent={added} classes={{ badge: classes.Badge }} color="primary">
										<span style={{ fontSize: '0.8rem', paddingLeft: '24px' }}>{added > 0 ? <strong>{option.name}</strong> : option.name}</span>
									</Badge>
								</>
							}}
							
							onChange={(props, device) => {
								
								// check if an item has been selected
								if (device) {
									
									// verify if device has certain state, if required by the module
									const states = Object.keys(device.states || {});
									if (moduleSettings.devicesStates && moduleSettings.devicesStates.requiredStates && Array.isArray(moduleSettings.devicesStates.requiredStates) && moduleSettings.devicesStates.requiredStates.some(state => states.indexOf(state) === -1)) {
										setError(i18n.t('Device needs one of the following states') + ': ' + moduleSettings.devicesStates.requiredStates.join(', '));
									}
									else {
										_addItem('device', device);
									}
								}
							}}
							
							groupBy={option => i18n.t(option.function)}
							closeIcon={null}
							popupIcon={null}
							/>
						
					</Inputs.FieldsetItem>
					{Array.isArray(moduleSettings['devices']) && moduleSettings['devices'].indexOf('group') > -1 && <><Inputs.FieldsetItem columns={1} centered>
						<em>{i18n.t('or')}</em>
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem columns={3}>
						<Inputs.Text
							width="90%"
							key='addGroup'
							id='addGroup'
							label='Add group'
							
							onEnter={(props, val) => val && _addItem('group', val)}
							clearOnEnter
							clearOnBlur
							/>
						
					</Inputs.FieldsetItem></>}
					{Array.isArray(moduleSettings['devices']) && moduleSettings['devices'].indexOf('divider') > -1 && <><Inputs.FieldsetItem columns={1} centered>
						<em>{i18n.t('or')}</em>
					</Inputs.FieldsetItem>
					<Inputs.FieldsetItem columns={3}>
						<Inputs.Button
							key='addDivider'
							id='addDivider'
							label='Add divider'
							onClick={() => _addItem('divider')}
							/>
						
					</Inputs.FieldsetItem></>}
				</Inputs.Fieldset>
				<Inputs.FieldsetItem>
					<List classes={{ root: classes.fullWidth }} dense disablePadding>
						{renderedDevices}
					</List>
				</Inputs.FieldsetItem>
				
			</Inputs.Fieldset>
		</>
	);
});

/*
 *
 */
const Device = React.memo(props => {
	Log._('WidgetPage', 'RENDER Device', 'development');
	const classes = useStyles();
	const { _editItem, _moveItem, _deleteItem, devices, device, options, moduleSettings } = props;
	
	const [componentConfig,setComponentConfig] = useState(null);
	
	let Device = options.deviceId;
	let Icon = <DevicesIcon classes={{ root: classes.iconCentered }} />
	let Actions = null;
	
	if (options.type === 'device' && device !== null) {
		Device = <span className={classes.title}>{device.name + ' (' + i18n.t(device.function) + ', #' + device.id + ')'}</span>
		
		if (!moduleSettings.DeviceActionsConfiguration) {
			const states = Object.keys(device.states || {}).map(state => ({ [state]: device.states[state].label || state }));
			
			Actions = (!moduleSettings.devicesStates || (moduleSettings.devicesStates && moduleSettings.devicesStates.selectable !== false)) && <Grid container spacing={1}>
					<Grid item xs={6}>
					{(!moduleSettings.devicesStates || !moduleSettings.devicesStates.selectable || (moduleSettings.devicesStates.selectable && moduleSettings.devicesStates.selectable.indexOf('primaryStateKey') > -1)) &&
						<Inputs.Select
							id="primaryStateKey"
							width={150}
							
							label='Primary State Key'
							selection={states}
							val={options.primaryStateKey !== undefined ? options.primaryStateKey : device.primaryStateKey}
							onChange={(props, val) => _editItem(options.index, props, val)}
							disabled={moduleSettings.devicesStates && moduleSettings.devicesStates.disabled && moduleSettings.devicesStates.disabled.indexOf('primaryStateKey') > -1}
							/>}
					
					</Grid>
					<Grid item xs={6}>
					{(!moduleSettings.devicesStates || !moduleSettings.devicesStates.selectable || (moduleSettings.devicesStates.selectable && moduleSettings.devicesStates.selectable.indexOf('secondaryStateKey') > -1)) ?
						<Inputs.Select
							id="secondaryStateKey"
							width={150}
							
							label='Secondary State Key'
							selection={[ { '': i18n.t('none') }, ...states ]}
							val={options.secondaryStateKey !== undefined ? options.secondaryStateKey : device.secondaryStateKey}
							onChange={(props, val) => _editItem(options.index, props, val)}
							disabled={moduleSettings.devicesStates && moduleSettings.devicesStates.disabled && moduleSettings.devicesStates.disabled.indexOf('secondaryStateKey') > -1}
							/> :
						<Placeholder width={150} />}
					</Grid>
					{moduleSettings.devicesStates && moduleSettings.devicesStates.lastUpdateOption === true &&
					<Grid item xs={12}>
						<Inputs.Switch
							id="lastUpdate"
							label='Display Last Update'
							
							val={options.lastUpdate !== undefined ? options.lastUpdate : true}
							onChange={(props, val) => _editItem(options.index, props, val)}
							/>
						
					</Grid>}
				</Grid>
		}
	}
	else if (options.type === 'group') {
		Icon = <GroupIcon classes={{ root: classes.iconCentered }} />
		Device = <span className={classes.title}>{options.name}</span>
	}
	else if (options.type === 'divider') {
		Icon = <DividerIcon classes={{ root: classes.iconCentered }} />
		Device = <em className={classes.title}>{'(' + i18n.t('divider') + ')'}</em>
	}
	
	const deviceOptions = {
		device: device || {},
		options: devices[options.index] || options,
		onChange: (props, val) => _editItem(options.index, props, val),
		onConfig: (props, config) => setComponentConfig(config),
		elements: { Icon, Device, Actions }
	}
	
	return (
		<>
			<ComponentConfig
				_onChange={(props, val) => _editItem(options.index, props, val)}
				config={componentConfig}
				/>
			
			<ListItem key={'Device-' + (options.deviceId || options.type) + '-' + options.index} divider classes={{ container: classes.height }}>
				<Grid container item xs={1}>
					<div className={classes.iconContainer}>
						<IconButton size="small" onClick={() => _moveItem('up', options.index)}>
							<ArrowUpIcon />
						</IconButton>
						{/*<IconButton size="small">
							<DragIcon />
						</IconButton>*/}
						<IconButton size="small" onClick={() => _moveItem('down', options.index)}>
							<ArrowDownIcon />
						</IconButton>
					</div>
					<IconButton size="small" onClick={() => _deleteItem(options.index)} style={{ height: '30px', margin: 'auto 0 auto 8px' }}>
						<DeleteIcon />
					</IconButton>
				</Grid>
				<Grid item xs={6}>
					{moduleSettings.DeviceBodyConfiguration ? React.createElement(moduleSettings.DeviceBodyConfiguration, deviceOptions) : <>{Icon} {Device}</>}
				</Grid>
				<Grid item xs={5}>
					{moduleSettings.DeviceActionsConfiguration ? React.createElement(moduleSettings.DeviceActionsConfiguration, deviceOptions) : Actions}
				</Grid>
			</ListItem>
		</>
	);
});

/*
 *
 */
const ComponentConfig = React.memo(props => {
	Log._('WidgetPage', 'RENDER ComponentConfig', 'development');
	//const classes = useStyles();
	const { _onChange, config } = props;
	
	const [open,setOpen] = useState(false);
	useEffect(() => {
		setOpen(config !== null);
	}, [config]);
	
	if (config === null) {
		return null;
	}
	
	// component configuration
	const componentConfiguration = Object.keys(config).map(configKey => {
		const configOption = config[configKey];
		
		return (
			<ListItem key={'Config-' + configKey} divider>
				<ListItemText primary={configOption.label || configKey} />
				<ListItemSecondaryAction>
					<Inputs.Text
						key={'componentConfig.' + configKey}
						id={'componentConfig.' + configKey}
						
						width={100}
						inputProps={{
							style: { textAlign: 'right' }
						}} 
						
						onChange={(props, val) => _onChange(props, val)}
						val={configOption.value || configOption}
						/>
					
				</ListItemSecondaryAction>
			</ListItem>
		);
	});
	
	return (
		<Dialog
			header={i18n.t('Component configuration')}
			actions={
				<Inputs.Button key="action-save" variant="text" onClick={() => setOpen(false)} color="primary" label="Apply" />
			}
			
			dense
			maxWidth='xs'
			fullWidth={true}
			open={open}
			onClose={() => setOpen(false)}
			>
			
			<List dense disablePadding>
				{componentConfiguration}
			</List>
		</Dialog>
	);
});
