import React, { useState, useEffect, useRef, useCallback } from 'react'
//import clsx from 'clsx'

import DevicesConfig, { DevicesStates } from './Devices.config'
import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
import FUNCTIONS from '../../../functions'
import ucFirst, { moveKeyInObject } from '../../../helpers/utils'

import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'

import IconButton from '@material-ui/core/IconButton'

import AddIcon from '@material-ui/icons/Add'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import DeleteIcon from '@material-ui/icons/Delete'

import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%',
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
}));


const DevicesPage = React.memo(props => {
	const classes = useStyles();
	
	const [device,setDevice] = useState(props.device);
	const [errors,setErrors] = useState({});
	const EDITOR = useRef({});
	
	
	/**
	 *
	 */
	const _onChange = useCallback((props, val) => {
		
		// error occured
		if (props.error && props.error !== errors[props.id]) {
			setErrors(prevErrors => ({
				...prevErrors,
				[props.id]: props.error
			}));
		}
		
		// no error -> remove old error
		else if (!props.error && errors[props.id]) {
			setErrors(prevErrors => {
				let nextErrors = { ...prevErrors || {} }
				delete nextErrors[props.id];
				return nextErrors;
			});
		}
		
		// set value
		EDITOR.current[props.id] = val;
		
	}, [errors]);
	
	/*
	 *
	 */
	useEffect(() => {
		_onChange({ error: !device.name, id: 'device-name' }, device.name || ''); // trigger initial validation
	}, [_onChange, device.name]);
	
	if (device === null) {
		return null;
	}
	
	/**
	 *
	 */
	const _onSave = () => {
		let savedDevice = { ...device }
		savedDevice.options = savedDevice.options || {}
		savedDevice.attributes = savedDevice.attributes || {}
		
		for (const key in EDITOR.current) {
			const val = EDITOR.current[key];
			const [ type, base, characteristic ] = key.split('-');
			
			// states
			if (type === 'states') {
				if (typeof savedDevice[type][base] === 'object') {
					savedDevice[type][base][characteristic] = val;
				}
				else {
					savedDevice[type][base] = {
						state: savedDevice[type][base],
						[characteristic]: val
					}
				}
			}
			
			// options or attributes
			else if (type === 'options' || type === 'attributes') {
				savedDevice[type][base] = val;
			}
			
			// device
			else {
				savedDevice[base] = val;
			}
		}
		
		// save
		savedDevice.attributes._updated = Date.now();
		props.onSave && props.onSave(savedDevice);
	}
	
	/**
	 *
	 */
	const _deleteDevice = () => props.onDelete && props.onDelete(device.id);
	
	/**
	 *
	 */
	const _addState = newStateKey => {
		
		// empty state key
		if (!newStateKey) {
			setErrors(prevErrors => ({
				...prevErrors,
				'newStateKey': i18n.t('Type in a state key / identifier')
			}));
		}

		// state key exists already
		else if (Object.keys(device.states || {}).indexOf(newStateKey) > -1) {
			setErrors(prevErrors => ({
				...prevErrors,
				'newStateKey': i18n.t('State key already used')
			}));
		}
		
		// add state
		else {
			const key = newStateKey.replace(/ /g, '');
			setDevice(prevDevice => ({
				...prevDevice,
				states: {
					[key]: {},
					...prevDevice.states || {}
				}
			}));
		}
	}
	
	/**
	 *
	 */
	const _moveState = (action, key) => {
		
		setDevice(prevDevice => ({
			...prevDevice,
			states: moveKeyInObject({ ...prevDevice.states }, key, action)
		}));
	}
	
	/**
	 *
	 */
	const _deleteState = key => {
		
		setDevice(prevDevice => {
			let nextDevice = { ...prevDevice }
			delete nextDevice.states[key];
			return nextDevice;
		});
	}
	
	/*
	 *
	 */
	const stateKeys = Object.keys(device.states || {});
	Log._('DevicesPages', 'RENDER DevicesPage', 'development');
	
	return (
		<Dialog
			header={i18n.t('Device Configuration') + ' (' + i18n.t('Device ID') + ': ' + device.id + ')'}
			actions={[
				<Grid key='actions' container>
					<Grid item xs={6}>
						{device.name && <Inputs.Button key='action-delete' variant="text" onClick={() => _deleteDevice()} color="secondary" startIcon={<DeleteIcon />} label="Delete Device" />}
					</Grid>
					<Grid item xs={6} className={classes.alignRight}>
						<Inputs.Button key='action-cancel' variant="text" onClick={() => props.onClose()} color="secondary" label="Cancel" />
						<Inputs.Button key='action-save' variant="text" onClick={() => _onSave()} color="primary" disabled={Object.keys(errors).length > 0} label="Apply" />
					</Grid>
				</Grid>
			]}
			
			dense
			maxWidth='md'
			fullWidth={true}
			
			open={true}
			onClose={() => props.onClose()}
			>
			
			<Inputs.Fieldset label={'General'}>
				<Inputs.FieldsetItem columns={4}>
					<Inputs.Text
						key="device-name"
						id="device-name"
						autoFocus
						required
						
						label='Device'
						val={device.name}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
				<Inputs.FieldsetItem columns={4}>
					<Inputs.Select
						key="device-function"
						id="device-function"
						
						label='Function'
						val={device.function || 'other'}
						selection={
							Object.keys(FUNCTIONS).map(funcKey => {
								const func = funcKey !== '_defaults' ? funcKey : 'other';
								return {
									[func]: i18n.t(func),
									'icon': (FUNCTIONS[funcKey]._any && FUNCTIONS[funcKey]._any.icon) || null,
									'_key': func,
									'_translated': true,
									'_sort': i18n.t(func)
								}
							})
						}
						sortOptionsByKey
						
						onChange={(props, val) => {
							
							// add default state keys of function (if not added already)
							if (FUNCTIONS[val] !== undefined) {
								Object.keys(FUNCTIONS[val]).forEach(stateKey => {
									
									if (stateKeys.indexOf(stateKey) === -1 && stateKey.substr(0,1) !== '_') {
										_addState(stateKey);
									}
								});
							}
							
							_onChange(props, val);
						}}
						/>
					
				</Inputs.FieldsetItem>
				<Inputs.FieldsetItem columns={4}>
					<Inputs.Text
						key="device-icon"
						id="device-icon"
						
						label='Icon'
						val={device.icon || ''}
						onChange={_onChange}
						/>
					
				</Inputs.FieldsetItem>
				
			</Inputs.Fieldset>
			
			<Inputs.Fieldset label='States'>
				<DeviceStates
					_onChange={_onChange}
					_addState={_addState}
					_moveState={_moveState}
					_deleteState={_deleteState}
					errors={errors}
					states={device.states}
					/>
					
			</Inputs.Fieldset>
			
			<Inputs.Fieldset label='Options'>
				<DeviceOptions
					_onChange={_onChange}
					options={device.options}
					/>
					
			</Inputs.Fieldset>
		</Dialog>
	)
});

export default DevicesPage;


/*
 *
 */
const DeviceOptions = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceOptions', 'development');
	const { options } = props;
	const classes = useStyles();
	
	const renderedOptions = Object.keys(DevicesConfig).sort().map(config => {
		return <DeviceOption key={'DeviceOption-' + config} config={{ ...DevicesConfig[config], parameter: config }} value={options[config] || ''} {...props} />
	});
	
	return (
		<List classes={{ root: classes.fullWidth }} dense>
			{renderedOptions}
		</List>
	);
});

/*
 *
 */
const DeviceOption = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceOption', 'development');
	const { config, value, _onChange } = props;
	
	let val = value;
	if (Array.isArray(val)) {
		val = val.join(',');
	}
	else if (typeof val === 'object') {
		val = JSON.stringify(val);
	}
	
	const Tag = Inputs[config.type || 'Text'];
	return (
		<ListItem key={'StatesListItemLabel-' + config.parameter} divider>
			<ListItemText
				primary={config.parameter}
				secondary={i18n.t(config.label || '')}
				/>
				
			<ListItemSecondaryAction>
				<Tag
					key={'options-' + config.parameter}
					id={'options-' + config.parameter}
					
					val={val}
					onChange={_onChange}
					{...config}
					/>
				
			</ListItemSecondaryAction>
		</ListItem>
	);
});

/*
 *
 */
const AddDeviceState = React.memo(props => {
	Log._('DevicesPages', 'RENDER AddDeviceState', 'development');
	const { errors, _addState } = props;
	const [newStateKey,setNewStateKey] = useState('');
	
	return (
		<ListItem key={'StatesListItemLabel-add'} dense divider>
			<ListItemIcon>
				<IconButton size="small" disabled>
					<AddIcon />
				</IconButton>
			</ListItemIcon>
			
			<ListItemText disableTypography>
				<Inputs.Text id={'states-add'} onChange={(props, input) => setNewStateKey(input)} shrink={true} label='new state key' val={newStateKey} />
				<Inputs.Button
					label='add state'
					onClick={() => _addState(newStateKey)}
					/>
				
				<span style={{ color: '#900' }}>{errors['newStateKey']}</span>
			</ListItemText>
		</ListItem>
	);
});

/*
 *
 */
const DeviceStates = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceStates', 'development');
	const { states } = props;
	const classes = useStyles();
	
	const renderedStates = Object.keys(states || {}).map(stateKey => {
		let state = states[stateKey];
		state = typeof state === 'string' ? { 'state': state } : state;
		return <DeviceState key={'DeviceState-' + stateKey} state={{ ...state, key: stateKey }} {...props} />
	});
	
	return (
		<>
			<List classes={{ root: classes.fullWidth }} dense>
				<AddDeviceState {...props} />
				{renderedStates}
			</List>
		</>
	);
});

/*
 *
 */
const DeviceState = React.memo(props => {
	Log._('DevicesPages', 'RENDER DeviceState', 'development');
	const { state, _onChange, _moveState, _deleteState } = props;
	const classes = useStyles();
	
	const states = Object.keys(DevicesStates).map(key => {
		const s = DevicesStates[key];
		const Input = Inputs[s.type || 'Text'];
		return (
			<Inputs.FieldsetItem columns={4} key={'states-' + state.key + '-' + key}>
				<Input
					{...s}
					id={'states-' + state.key + '-' + key}
					shrink={true}
					label={s.label || ucFirst(key)}
					
					val={!state[key] || typeof state[key] === 'string' ? (state[key] || '') : (state[key].node || JSON.stringify(state[key]))}
					onChange={(props, val) => typeof s.onChange === 'function' ? s.onChange(props, val, _onChange) : _onChange(props, val)}
					/>
					
			</Inputs.FieldsetItem>
		);
	});
	
	return (
		<ListItem key={'StatesListItemLabel-' + state.key} dense divider>
			{state.key &&
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<IconButton size="small" onClick={() => _moveState('up', state.key)}>
					<ArrowUpIcon />
				</IconButton>
				<IconButton size="small" onClick={() => _moveState('down', state.key)}>
					<ArrowDownIcon />
				</IconButton>
				<IconButton size="small" onClick={() => _deleteState(state.key)} style={{ marginTop: 40 }}>
					<DeleteIcon />
				</IconButton>
			</ListItemIcon>}
			
			<ListItemText disableTypography>
				<div style={{ paddingBottom: 8 }}><strong>{state.key}</strong></div>
				<Inputs.Fieldset noPadding>
					{states}
				</Inputs.Fieldset>
			</ListItemText>
		</ListItem>
	);
});
